import { RefreshOrLoadState } from "./RefreshOrLoadState";
import { _decorator, Component, Sprite, Label } from 'cc';
const { ccclass, property } = _decorator;

@ccclass
export default class JmRefreshFooter extends Component {

    @property(Label)
    loading: Label = null;
    @property(Sprite)
    emptyImg: Sprite = null;

    setState(state: RefreshOrLoadState) {
        switch (state) {
            case RefreshOrLoadState.NONE:
            case RefreshOrLoadState.EMPTY:
                this.loading.node.active = false
                this.emptyImg.node.active = false
                break;
            case RefreshOrLoadState.READY_TO_REFRESH:
                break;
            case RefreshOrLoadState.REFRESHING:
                this.loading.node.active = true
                this.loading.string = ""
                this.emptyImg.node.active = false
                break;
            case RefreshOrLoadState.COMPLETE:
                this.emptyImg.node.active = true
                this.loading.node.active = false
                break
        }
    }

    start() {

    }
}
