import JmListView from "./JmListView";
import JmHorizontalListView from "./JmHorizontalListView";
import { Component, EventTouch, Node, Vec2, _decorator } from "cc";
import BaseView from "db://app/base/BaseView";

const { ccclass } = _decorator;

@ccclass
export default class JmListViewItem extends BaseView {
    protected itemId: number
    protected listView: JmListView | JmHorizontalListView;
    protected performClick = false
    private startPos: Vec2//Touch

    protected onLoad() {
        super.onLoad && super.onLoad()
    }

    protected onEnable() {
        this.node.on(Node.EventType.TOUCH_START, this.onTouchEvent, this)
        this.node.on(Node.EventType.TOUCH_MOVE, this.onTouchEvent, this)
        this.node.on(Node.EventType.TOUCH_END, this.onTouchEvent, this)
    }

    protected onDisable() {
        this.node.off(Node.EventType.TOUCH_START, this.onTouchEvent, this)
        this.node.off(Node.EventType.TOUCH_MOVE, this.onTouchEvent, this)
        this.node.off(Node.EventType.TOUCH_END, this.onTouchEvent, this)
    }

    onTouchEvent(event: EventTouch) {
        switch (event.getType()) {
            case Node.EventType.TOUCH_START:
                // JmLobbyLog.Log("TOUCH_START")
                this.performClick = true
                this.startPos = event.getLocation()
                break
            case Node.EventType.TOUCH_MOVE:
                // JmLobbyLog.Log("TOUCH_MOVE")
                let pos = event.getLocation()
                let dstx = Math.abs(this.startPos.x - pos.x)
                let dsty = Math.abs(this.startPos.y - pos.y)
                if (dstx > 5 || dsty > 5) {//TOUCH_MOVE
                    this.performClick = false
                }

                // let dst: Vec2 = this.startPos.sub(pos)
                // if (dst.len() > 5) {//TOUCH_MOVE
                //     this.performClick = false
                // }
                break
            case Node.EventType.TOUCH_END:
                if (this.performClick) {
                    this.onItemClick(event)
                }
                this.performClick = false
                break
        }
    }

    onItemClick(event?: EventTouch) {
        // JmLobbyLog.Log("ItemClicked, itemId-->" + this.itemId)
    }

    updateItem(id: number, data: any, listView: JmListView | JmHorizontalListView) {
        this.itemId = id
        this.listView = listView
        //
    }

    getItemHeight(): number {
        return undefined
    }

    getItemWidth(): number {
        return undefined
    }

}