import { Component, Skeleton, _decorator } from "cc";
import { RefreshOrLoadState } from "./RefreshOrLoadState";

const { ccclass, property } = _decorator;

@ccclass
export default class JmRefreshHeader extends Component {
    @property(Skeleton)
    refreshSpine: Skeleton = null

    private _state = RefreshOrLoadState.NONE

    start() {

    }

    onScrollOffset(offsetY: number) {
        if (offsetY < -40) {
            offsetY = -40
        }
        this.refreshSpine.node.y = offsetY - 50
    }

    setState(state: RefreshOrLoadState) {
        this._state = state
        // switch (state) {
        //     case RefreshOrLoadState.NONE:
        //         this.refreshSpine.setAnimation(0, "refresh_1", false)
        //         break;
        //     case RefreshOrLoadState.READY_TO_REFRESH:
        //         this.refreshSpine.setAnimation(0, "refresh_1", false)
        //         break;
        //     case RefreshOrLoadState.REFRESHING:
        //         this.refreshSpine.setAnimation(0, "refresh_2", true)
        //         break;
        // }
    }

}
