/**
 * ,
 * */
import JmRoomData from "../data/JmRoomData";
export default class JmRoomDataMgr {
    private static _roomsData: Map<string, JmRoomData> = new Map<string, JmRoomData>();
    private static _roomsKeys: Array<string> = [];
    private static _joinRoomsList: Array<string> = []; //
    private static _offset: number = 0;
    private static _currentRoomData: object;
    private static _currentRoomKey: number;
    private static currentType: number = 0;
    private static _roomCfg: Map<number, number> = new Map<number, number>();
    private static _roomCount: number = 0;
    private static _gameState: number = 2;//0 1 2
    private static _isDay: boolean = true;
    private static _isOwner: boolean = false;
    private static _loadKey: boolean = true;
    /**
     * */
    static OnReceiveServerData(serverData:any) {
        this.InitRoomsData(serverData);
    }

    static InitRoomsData(data: Array<object>) {
        for (let i = 0, len = data.length; i < len; i++) {
            this.InitRoomData(data[i]);
        }
    }
    
    static InitRoomData(data: any) {
        let roomKey = data["recordId"];
        if (roomKey) {
            let room = this.GetRoom(roomKey);
            room.initRoomData(data);
        }
    }

    static CreateRoom(roomData: object) {
        this.InitCurrentRoom(roomData);
    }

    static CleanRooms() {
        this._offset = 0;
        this._roomsData.clear();
        this._roomCfg.clear();
        this._roomsKeys = [];
        this._gameState = 2
    }

    static CleanRoom(roomKey: string) {
        let room = this._roomsData.get(roomKey);
        if (room) {
            room = null;
            this._roomsData.delete(roomKey);
        }
    }

    static GetOffSet() {
        return this._offset || 0;
    }

    static GetRoomByRoomKey(roomKey:any) {
        let room = this._roomsData.get(roomKey);
        if (room) {
            return room;
        }
        return null;
    }

    static GetRoomNum() {
        let roomData = this.GetRoomByRoomKey(this._currentRoomKey);
        return roomData.getRoomNum();
    }
    
    static GetRoomIdentities() {
        let roomData = this.GetRoomByRoomKey(this._currentRoomKey);
        return roomData.getIdentities();
    }

    static GetRoomIdentity() {
        let roomData = this.GetRoomByRoomKey(this._currentRoomKey);
        return roomData.getIdentity();
    }

    static GetRoomSeats() {
        let roomData = this.GetRoomByRoomKey(this._currentRoomKey);
        return roomData.getSeats();
    }

    static InitCurrentRoomProto(roomData: any) {
        let roomKey = roomData["recordId"]
        this._roomCount = roomData["players"]
        let room = this.GetRoom(roomKey);
        room.initRoomData(roomData);
        this._gameState = roomData["status"]
        this._currentRoomData = roomData;
        this._currentRoomKey = roomKey;
        this._joinRoomsList.unshift(roomData["roomNum"]);
    }

    //
    static InitCurrentRoom(roomData: any) {
        console.log(`JmRoomDataMgr.InitCurrentRoom ${JSON.stringify(roomData)}`);

        let roomKey = roomData["recordId"]
        this._isOwner = roomData["judge"]
        this._roomCount = roomData["players"]
        let room = this.GetRoom(roomKey);
        room.initRoomData(roomData);
        this._gameState = roomData["status"]
        this._currentRoomData = roomData;
        this._currentRoomKey = roomKey;
        this._joinRoomsList.unshift(roomData["roomNum"]);
    }

    static GetRoom(roomKey: string) {
        let room = this._roomsData.get(roomKey);
        if (!room) {
            room = new JmRoomData();
            this._roomsData.set(roomKey, room);
            this._roomsKeys.push(roomKey);
        }
        return room;
    }

    static GetCurrentRoomInfo() {
        return this._currentRoomData;
    }

    static GetCurrentRoomKey() {
        return this._currentRoomKey || 0;
    }


    static SetCurrentNullRoomKey() {
        this._currentRoomKey = null;
    }

    static SetRoomDayOrNight(currentType:number) {
        this.currentType = currentType;
    }
    static GetRoomDayOrNight() {
        return this.currentType;
    }
    static delRoomCfg(key:any) {
        this._roomCfg.delete(key);
    }
    static setRoomCfg(key:any, vule:any) {
        this._roomCfg.set(key, vule);
    }
    static getRoomCfg() {
        return this._roomCfg;
    }

    static getRoomCount() {
        return this._roomCount
    }
    static setRoomCount(count:number) {
        this._roomCount = count
    }

    static setGameState(state:number) {
        this._gameState = state
    }
    static getGameState() {
        return this._gameState
    }

    static setGameDay(bol:boolean) {
        this._isDay = bol
    }
    static getGameDay() {
        return this._isDay;
    }

    static setOwner(bol:boolean) {
        this._isOwner = bol
    }
    static isOwner() {
        return this._isOwner
    }
    static getLoadKey() {
        return this._loadKey
    }
    static setLoadKey(bol:boolean) {
        this._loadKey = bol
    }
}


