import { Component, instantiate, isValid, NodePool, Prefab } from "cc";

export class JmNodePool extends NodePool {
    private _cellPfb: Prefab;
    get cellPfb() {
        return this._cellPfb;
    }

    set cellPfb(value) {
        this._cellPfb = value;
    }
}

export default class JmNodePoolMgr {
    private static _nodePoolData: Map<string, JmNodePool> = new Map<string, JmNodePool>();

    static IdentityCellPool: string = "IDENTITY_CELL_POOL";

    /**
     * */
    static InitNodePool(poolKey: string, cellPfb: Prefab, poolHandlerComp?: string) {
        this.DestroyPool(poolKey);

        let pool = new JmNodePool(poolHandlerComp);
        pool.cellPfb = cellPfb;
        this._nodePoolData.set(poolKey, pool);
    }

    static GetCell(poolKey: string) {
        let pool = this.GetPool(poolKey);
        if (pool) {
            let node;
            if (pool.size()) {
                node = pool.get();
            } else {
                node = instantiate(pool.cellPfb);
            }
            node.active = true;
            return node;
        }
    }

    static PutCell(poolKey: string, node: any) {
        let pool = this.GetPool(poolKey);
        if (pool) {
            node.active = false;
            pool.put(node);
        }
    }

    static DestroyPool(poolKey: string) {
        let pool = this._nodePoolData.get(poolKey);
        if (pool) {
            for (let i = 0; i < pool.size(); i++) {
                let node = pool.get();
                if (isValid(node, true)) {
                    node.active = false;
                    node.destroy();
                }
            }
            pool.clear();
            this._nodePoolData.delete(poolKey);
        }
    }

    static GetPool(poolKey: string) {
        let pool = this._nodePoolData.get(poolKey);
        if (pool) {
            return pool;
        } else {
            console.log(`JmNodePoolMgr.GetPool!The pool is null! please instance it! ${poolKey}`);
        }
    }
}


